import { MenuList, StyledLink, MenuItem } from "./Header.styled";
import DecorLine from "../decor/DecorLine";

export default function RenderMenuList ({tablet, desktop}) {
    return (
        <MenuList tablet={tablet} desktop={desktop}>
        <StyledLink to='/dobros-design' desktop={desktop}>
            <MenuItem tablet={tablet}>Головна</MenuItem>
            <DecorLine tablet={tablet}/>
        </StyledLink>
        <StyledLink to='/products' desktop={desktop}>
            <MenuItem tablet={tablet}>Продукція</MenuItem>
            <DecorLine tablet={tablet}/>
        </StyledLink>
        <StyledLink to='/saved' desktop={desktop}>
            <MenuItem tablet={tablet}>Збережене</MenuItem>
            <DecorLine tablet={tablet}/>
        </StyledLink>
        <StyledLink to='/works' desktop={desktop}>
            <MenuItem tablet={tablet}>Відгуки</MenuItem>
            <DecorLine tablet={tablet}/>
        </StyledLink>
        <StyledLink to='/contacts' desktop={desktop}>
            <MenuItem>Контакти</MenuItem>
        </StyledLink>
    </MenuList>

    );
}